import { get, post } from "../http";
const home = {
  testPost(data) {
    return post("/post", data);
  },
  testGet(data) {
    return get("/get", data);
  },
  testPut(data) {
    return put("/put", data);
  },
  testDel(data) {
    return DELETE("/delete", data);
  }
};
export default home;



