<template>
  <div>
    <van-button @click="post" type="primary">post</van-button>
    <van-button @click="get" type="primary">get</van-button>
    <!-- <div @click="put">put</div>
    <div @click="del">del</div> -->
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    post() {
      this.$http.home
        .testPost({
          aaa: 11
        })
        .then(res => {
          console.log(res);
        });
    },
    get() {
      this.$http.home
        .testGet({
          aaa: 11
        })
        .then(res => {
          console.log(res);
        });
    },
    del() {
      this.$http.home
        .testDel({
          aaa: 11
        })
        .then(res => {
          console.log(res);
        });
    },
    put() {
      this.$http.home
        .testPut({
          aaa: 11
        })
        .then(res => {
          console.log(res);
        });
    }
  },
  created() {}
};
</script>

<style scoped lang="less"></style>



